import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Socket } from 'ngx-socket-io';
import { Observable } from 'rxjs';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { PedidosService } from '../../../providers/Api/Pedidos.service';
import { IonContent, ModalController, Platform, PopoverController } from '@ionic/angular';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { CargaElementosUsuariosService } from '../../../providers/cargas/usuarios.service';
import { ModalChatComponent } from '../../../Components/modal-chat/modal-chat.component';
import { DetallesComponent } from '../../../Components/detalles/detalles.component';

@Component({
  selector: 'app-salas-chat',
  templateUrl: './salas-chat.page.html',
  styleUrls: ['./salas-chat.page.scss'],
  animations: [
    trigger('escribiendo', [
      state('activo', style({
        opacity: 1,
        background: '#BDC3C6'
      })),
      state('noactivo', style({
        opacity: 1,
        background: '#ffffff'
      })),
      transition('activo <=> noactivo', [
        animate('300ms')
      ])
    ])
  ]
})
export class SalasChatPage implements OnInit {

  sala;
  id;

  mensaje = '';
  mensajes = [];
  desde = 0;

  carga = true;
  sala2;

  escribiendo = false;
  frenar = false;
  parar = false;
  cargamsg = false;
  @ViewChild('contenido', {static: false}) contenido: IonContent;
  constructor(
    private almacenamiento: InicioService,
    private router: Router,
    private toast: ToastServices,
    private socket: Socket,
    private apiPedidos: PedidosService,
    private cargarElementos: CargaElementosUsuariosService,
    private plt: Platform,
    private modal: ModalController,
    private popover: PopoverController
  ) { 
      this.escucharEnvios().subscribe(async (data: any) => {
        console.log(data);

        if (!this.parar) {
          if (data.exe) {
            this.mensaje = '';
            data.resp.ms = false;
            data.resp.vs = false;
            this.mensajes.push(data.resp);
           
  
            const info = await this.almacenamiento.obtenerToken();
  
            if (info) {
              console.log('entrando', info)
              this.contenido.scrollToBottom().then(() => {
    
              }).catch(() => {
    
              })
              if (data.resp.creador._id !== info._id) {
                console.log('marcar', data, this.parar);
              if (!this.parar) {
                this.marcar(data.resp._id, (this.mensajes.length - 1));
              }
              } else {
                
              }
            
            }
            
          }
        }
      })

      this.escucharVistos().subscribe((data: any) => {
        console.log(data, 'vistos');

        if (!this.parar) {
          if (data.exe) {
            this.mensajes.map((item) => {
              if (item._id === data.id) {
                item.vistos = item.vistos.filter((it) => {
                  return it !== data.usuario;
                });
              }
            })
          }
        }
      })


      this.escucharEscritoss().subscribe((data: any) => {
       console.log('escribiendo...');
       if (data.exe) {
          this.escribiendo = true;
          setTimeout(() => {
          this.escribiendo = false;
         }, 1000);
        }
      })
  
      this.cargarElementos.$cargarRed.subscribe(async (red: any) => {
   
        if (red === 'none') {
          this.almacenamiento.eleminarSala().then(() => {
            this.toast.Especial('Perdiste la conexión, verifica la red de internet e intenta nuevamente')
            this.router.navigate(['/central/chats'])
            this.socket.emit('salidachat', {id: this.sala._id});
         }).catch(() => {
           this.toast.Especial('Perdiste la conexión, verifica la red de internet e intenta nuevamente')
           this.router.navigate(['/central/chats'])
           this.socket.emit('salidachat', {id: this.sala._id});
         })
        }
      });
    
      this.plt.resume.subscribe( async (e) => {
       if (!this.parar) {
        this.contenido.scrollToBottom().then(() => {
          this.arranca();
        }).catch(() => {
         this.arranca();
        })
       }
    });

    
    }

  ngOnInit() {
  }

  ionViewWillEnter() {
  this.parar = false;
   this.arranca();
  }

  arranca() {
    this.mensajes = [];
    this.almacenamiento.obtenerToken().then((info) => {
      this.id = info._id;
      this.almacenamiento.obtenerSala().then((sala: any) => {

        if (sala.length > 0) {

        console.log(sala, 'info');
        this.sala = sala[0];
        this.sala2 = sala[0];
        this.socket.emit('iniciochat', {id: this.sala._id});
  
        if (this.sala.creador.imagen[0].path === 'notfound.png') {
          this.sala.creador.imagen[0].path = '/assets/notfound.png';
        } else {
  
          if (this.sala.creador.imagen[0].path.includes('https://www.motocaliapp.com')) {
            this.sala.creador.imagen[0].path = this.sala.creador.imagen[0].path;
          } else {
            this.sala.creador.imagen[0].path = 'https://www.motocaliapp.com/' + this.sala.creador.imagen[0].path;
          }
         
        }
  
        if (this.sala.taller.imagen[0].path === 'notfound.png') {
          this.sala.taller.imagen[0].path = '/assets/notfound.png';
        } else {
  
          if (this.sala.taller.imagen[0].path.includes('https://www.motocaliapp.com')) {
            this.sala.taller.imagen[0].path = this.sala.taller.imagen[0].path;
          } else {
            this.sala.taller.imagen[0].path = 'https://www.motocaliapp.com/' + this.sala.taller.imagen[0].path;
          }
  
         
        }
  
        this.cargarmensajesVistos();
        
      } else {
        this.router.navigate(['/central/chats'])
      }
      })

      
      })
  }

  refrescando(event) {
      event.detail.complete()
  }

  async cargaMensajesLimite(event) {
    this.desde = this.desde + 15;
    // console.log(this.desde)
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

      this.apiPedidos.traermensajesNoVistos(info.token, {idsala: this.sala._id, desde: this.desde, accesos: this.sala.accesos }).subscribe((data: any) => {
        console.log(data);
        if (data.exe) {

          if (data.response.length > 0) {
            event.target.complete();
            data.response.forEach((item) => {
              item.ms = false;
              item.vs = false;
              this.mensajes.unshift(item);
            });
      
          } else {
            event.target.disabled = true;
            this.desde = 0;
           
          }
       
       
        } else {
          this.toast.toastError('No se pudo traer los mensajes, inténtelo más tarde')
        }
      }, (err) => {
        this.toast.toastError('No se pudo traer los mensajes, inténtelo más tarde')
      })

    }
  }

  async cargarmensajes() {

    const info = await this.almacenamiento.obtenerToken();
 
    if ( info ) {
      this.apiPedidos.mensajes(info.token, {idsala: this.sala._id, accesos: this.sala.accesos}).subscribe((data: any) => {
        console.log(data);
        if (data.exe) {
         
          if (data.response.length > 0) {
            this.mensajes.push({ms: true, creador: {_id: ''}});
          }
          data.response.forEach((item) => {
            item.ms = false;
            item.vs = false;
            this.mensajes.push(item);
            this.marcar(item._id, (this.mensajes.length - 1));
            this.socket.emit('vistoChat', {
              idsala: this.sala._id,
              usuario: info._id,
              id: item._id
            })

          })

          this.cargamsg = true;

        } else {
          this.toast.toastError('No se pudo traer los mensajes, inténtelo más tarde')
        }
      }, (err) => {
        this.toast.toastError('No se pudo traer los mensajes, inténtelo más tarde')
      })
    }

  }



  
  async cargarmensajesVistos() {

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.apiPedidos.traermensajesNoVistos(info.token, {idsala: this.sala._id, desde: this.desde}).subscribe((data: any) => {
        console.log(data);
        if (data.exe) {
          data.response.forEach((item) => {
            item.ms = false;
            this.mensajes.unshift(item);
          });
          this.contenido.scrollToBottom().then(() => {
            this.cargarmensajes();
          }).catch(() => {
            this.cargarmensajes();
          })
       
        } else {
          this.toast.toastError('No se pudo traer los mensajes, inténtelo más tarde')
        }
      }, (err) => {
        this.toast.toastError('No se pudo traer los mensajes, inténtelo más tarde')
      })
    }

  }


  async marcar(id, idx) {

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.apiPedidos.marcarvisto(info.token, {id: id}).subscribe((data: any) => {
        console.log(data, 'MARCADO');
        if (data.exe) {
          this.mensajes[idx].vistos = this.mensajes[idx].vistos.filter((item) => {
              console.log(item, 'ITEMMMM', this.mensajes[idx])
              return  item !== info._id;
            
          });
          this.socket.emit('vistoChat', {
            idsala: this.sala._id,
            usuario: info._id,
            id: this.mensajes[idx]._id
          })
         // console.log(this.mensajes);
        } else {
          this.toast.toastError('No se marcó en visto el mensaje, inténtelo más tarde')
        }
      }, (err) => {
        this.toast.toastError('No se marcó en visto el mensaje, inténtelo más tarde')
      })
    }

  }

  escribir(event) {
    this.escribiendo = false;
    this.mensaje = event.detail.value;
    this.socket.emit('escribiendoChat', {idsala: this.sala._id})
  }

  escucharEnvios() {
    if (!this.frenar) {
      const myobserver = new Observable(observer => {
        this.socket.on('mensajeChat', (data) => {
          observer.next(data);
        });
      });
    
      return myobserver;
    }
  }

  escucharEscritoss() {
    if (!this.frenar) {
      const myobserver = new Observable(observer => {
        this.socket.on('escribiendoChat', (data) => {
          observer.next(data);
        });
      });
    
      return myobserver;
    }
  }


  escucharVistos() {
    if (!this.frenar) {
      const myobserver = new Observable(observer => {
        this.socket.on('vistoChat', (data) => {
          observer.next(data);
        });
      });
    
      return myobserver;
    }
  }


  enviarmensaje() {
      if (this.mensaje !== '') {
        let idenvio = '';
        let emisor = '';
        if (this.id !== this.sala.creador._id) {
          idenvio = this.sala.creador._id;
          emisor = this.sala.creador.nombreusuario;
        } else {
          idenvio = this.sala.taller._id;
          emisor = this.sala.taller.nombreusuario;
        }

        console.log(this.sala.accesos);

        this.socket.emit('mensajeChat', {idsala: this.sala._id, idenvio, emisor, id: this.id, msg: this.mensaje, sala: this.sala2, accesos: this.sala.accesos, emite: this.id});

       
     
      }
  }

  cerrar() {
    
  }

  ver(msg, i) {
    if (msg.creador._id === this.id) {

      if (this.mensajes[i].vs) {
        this.mensajes[i].vs = false;
      } else {
      this.mensajes[i].vs = true;
      }
    }
  }

  async abrir(eventt) {
    this.parar = true;
    const popover = await this.popover.create({
      component: DetallesComponent,
      componentProps: {
        id: this.sala._id
      },
      event: eventt
    })

    return await popover.present();
  }

  ionViewWillLeave() {
    this.parar = true;
    this.socket.emit('salidachat', {id: this.sala._id});
    this.almacenamiento.eleminarSala().then(() => {
       this.socket.emit('salidachat', {id: this.sala._id});
    }).catch(() => {
      this.socket.emit('salidachat', {id: this.sala._id});
    })
   
  }

}
